// force reload
@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@100..900&display=swap');
@import url(https://test.canada.ca/ircc/wurd-reporting/new-look-feel/css/progress-bar.css);
@import url(https://test.canada.ca/ircc/wurd-reporting/css/phases.css);
@import url(https://test.canada.ca/ircc/wurd-reporting/new-look-feel/css/user-journey-simple.css);
@import url(https://test.canada.ca/ircc/templates/wurd-crew/theme.css);

$primary: #350267;
$secondary: #600DB4;
$primary-contrast:#D5C6FF;
$secondary-contrast:#E9E5F2;
// $font-family: "Lexend", sans-serif;
// $font-family: "Noto", sans-serif;



.wurd, .wurd #wb-bc {
    background-color: $primary;
    color: #fff;
    // font-family: $font-family!important;
    font-optical-sizing: auto;
    font-style:normal;
    font-weight: 400;
    display: block;
    padding: 0 20px;
    margin-top:0;
}
    
.wurd {
    padding: 15px 0 25px;

    img {       
        padding-right: 20px;
        padding-left: 20px;        
    }

    h1, .h1-sub {        
        // font-family: $font-family!important;
        font-weight:500!important;
        font-size:2.5em;
        border: none!important;
        margin:0;
    }
    
    .h1-sub {
        font-size:1em;
        color: $primary-contrast
    }

    .background-logo {
        opacity: .5;
        position: relative;
        flex-grow: 1;

        .background-logo-svg {
            width: 25%;
            position:absolute;
            bottom: -120px;
            right: 50px;
        }
    }
}

.wurd #wb-bc {
    a {
        color: #fff; 
        &:hover, &:focus, &:visited {
            color:$primary-contrast;
        }
    }

    ol {
        margin-top: 0!important;
    }
    
    .breadcrumb li:before {
        color: #fff!important;
        padding-right: 5px;
    }
}

main {
    display: flex;
    flex-grow: 1;
}

#left-navigation {
    background-color: #f3f3f3;
    color: $primary!important;
    
    // font-family: $font-family;

    width: 17.5%;
    float:left;
    display: flex;
    flex-direction: column;

    h2 {
        background-color: $secondary-contrast;
        color: $primary;
        padding: 15px;
        padding-left:30px;
        margin:0;
        font-size: 1em;

        a {
            color: $primary!important;
            text-decoration: none!important;
            cursor:default;
        }
    }

    ul {
        list-style-type: none;
        padding:0;
        margin:0;

        li {
            border-bottom: 1px solid $primary;
            padding: 10px 15px 10px 35px;
            position:relative;

            a {
                text-decoration: none!important;
                color:$primary!important;
                font-family: "Lato", sans-serif;

                &:after {
                    background-color: rgba(0, 0, 0, 0);
                    bottom: 0;
                    content: "";
                    left: 0;
                    pointer-events: auto;
                    position: absolute;
                    right: 0;
                    top: 0;
                    z-index: 1;
                    font-weight: 300;
                }
            }
            
            &:has(a:hover, a:focus) {
                background-color:$primary-contrast;
            }
        }
    }
}

#mainContent {
    margin: 0 2.5%;
    width: 60%;
    float:left;
    display: block;

    h2::after {
        border-bottom: .18em solid $primary;
        content: "";
        display: block;
        padding-bottom: 8px;
        width: 70px;
    }

    .panel {
        padding:20px;
        border-radius: 20px;
    }

    .well {        
        border-radius: 20px;
        border:none;
        box-shadow: none;        
    }


    .project {
        text-align: left;
        margin-top: 20px;
        
        &.well {
            background-color: hsl(258, 33%, 99%);        
        }

        h3 {
            font-size: 1.5em;
            margin-bottom: 20px;
        }
    }

    hr {
       border-top: 3px solid $secondary-contrast;
       clear:both;
    }
}

@media (min-width: 1200px) {
    #mainContent {
        width: 80%;
    }

    #left-navigation {
        position: sticky!important;
        top:0;
        left:0;
    }
}

@media (max-width: 1199px) {

    .wurd h1, .wurd .sub-h1{
        font-size: 1.5em;
    }

    .background-logo {
        display:none;
    }

    main {
        flex-direction: column;
    }
    // 


    .wurd {
    
        img {       
            width:115px;      
        }
    }

    #mainContent {
        width: 100%;
        margin: 0;
        padding: 0 35px;
        clear:both;
    }

    #left-navigation {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        height: 100%;
        width:100%;       
        clear:both;

        h2 {
            position: relative;        
            flex-basis:1 1 20%;
            padding-right:35px;

            &:not(:last-child){
                border-right: 1px solid $primary-contrast;
            }
            
            &:has(a:hover, a:focus) {
                background-color:$primary-contrast;
            }
  
            a {
                cursor:pointer!important;

                &:hover, &:focus{
                    background: $primary-contrast;
                    color: $primary!important;
                }

                &:after {
                    background-color: rgba(0, 0, 0, 0);
                    bottom: 0;
                    content: "";
                    left: 0;
                    pointer-events: auto;
                    position: absolute;
                    right: 0;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        ul {
            flex-direction:column;
            order: 3;
            width: 100%;          
        }
        
    }
}


